<template>
  <div class="bottomRight list_box">
    <div id="bottomRight"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("bottomRight");
      var myChart = this.$echarts.init(chartDom);
      var option;

      option = {
        title: {
          text: "关键设备用能统计(kWh)",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "item",
          formatter: '{b}: {c}（{d}%）'
        },
        legend: {
          bottom: "15",
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            type: "pie",
            radius: "50%",
            center: ["50%", "50%"],
            roseType: "area",
            itemStyle: {
              borderRadius: 8,
            },
            data: [
              { value: 1048, name: "铁塔(办公楼)" },
              { value: 735, name: "一楼仓库(制造中心)" },
              { value: 580, name: "电梯(制造中心)" },
              { value: 484, name: "电梯(行政中心)" },
              { value: 300, name: "路灯" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            label: {
              color: "#fff",
              formatter: "{b}",
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#bottomRight {
  width: 100%;
  height: 100%;
}
.bottomRight {
  width: 28%;
  padding-top: 20px;
  margin-left: 10px;
  box-sizing: border-box;
}
</style>
